* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
  padding-left: 15px;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

p, ul {
  margin: 0;
}

ul {
  list-style: none;
  padding: 0;
}

span {
  font-weight: 700;

}

.country {
  display: flex;  
  align-items: center;
  margin-bottom: 5px;
}

.country-flag {
  margin-right: 10px;
  width: 30px;
  height: 30px;
}

.country-info > ul > li {
  margin-bottom: 10px;
}